import React from 'react';
import {connect} from 'react-redux';
import {startDealCard, removeCard} from '../actions/card';  
import {setDeck} from '../actions/deck';  
import {shuffle} from '../cardActions/cardActions';

export class ActionButton extends React.Component {
    constructor (props) {
        super(props);
        console.log(props.deck);
    }

    handleShuffle = () => {
        const deck = this.props.deck;
        if (deck.length === 0) {
            this.props.removeCard();
        }
        this.props.setDeck(shuffle(deck));
    }

    handleDeal = () => {
        this.props.startDealCard();
    }

    render () {
        return (
            <div>
                <button onClick={this.handleShuffle}>Shuffle</button>
                <button onClick={this.handleDeal}>Deal</button>
            </div>
            
        )
    }
}

const mapDispatchToProps = (dispatch) => {
	return {
        startDealCard: () =>  dispatch(startDealCard()),
        setDeck: (deck) => dispatch(setDeck(deck)),
        removeCard: () => dispatch(removeCard())
	}
}

const mapStateToProps = (state) => {
    return {
        card: state.card,
        deck: state.deck
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ActionButton);