import {removeFromDeck} from "./deck";

export const startDealCard = () => {

    return (dispatch, getState) => {
        let deck = getState().deck;
        if (deck.length > 0) {
            const cardIndex = Math.floor(Math.random() * getState().deck.length);
            dispatch(dealCard(deck[cardIndex]));
            dispatch(removeFromDeck(deck[cardIndex]));
        }
    }
	
}

export const dealCard = (card) => ({
    type: 'DEAL_CARD',
	card
});

export const removeCard = () => ({
    type: 'REMOVE_CARD'
});


