import React from 'react';
import {connect} from 'react-redux';

export class Card extends React.Component {
    constructor (props) {
        super(props);
    }
    
    render () {
        const propsCard = this.props.card;
        return (
            <div>
                {Object.keys(propsCard).length > 0 && <div><img src={'/imgs/deck/png/' + propsCard.value + propsCard.suit + '.png'} />{propsCard.value + propsCard.suit}</div>}
            </div>
            
        );
    }
    
}

const mapStateToProps = (state) => {
    return {
        card: state.card,
        deck: state.deck
    }
}

export default connect(mapStateToProps, undefined)(Card);