const defaultState = {
	deck: [],
	card: {},
	isFull: null,
	isEmpty: null
};


export default (state = defaultState.deck, action) => {

	switch (action.type) {
		case 'SET_DECK':
			return action.deck;
		case 'REMOVE_FROM_DECK':
			const ned = state.filter((card) => {
				return JSON.stringify(card) !== JSON.stringify(action.card);
			});
			console.log(ned);
			return ned;	
		default:
			return state;
	}
}